<script setup>
import { onMounted } from 'vue';  // 添加生命周期钩子导入

function copyCommand() {
    const commandText = "git clone https://github.com/xiaoce-2025/QtCalendar.git";
    
    // 降级实现
    const copyFallback = () => {
        const textarea = document.createElement('textarea');
        textarea.value = commandText;
        textarea.style.position = 'fixed';
        textarea.style.opacity = '0';
        document.body.appendChild(textarea);
        textarea.select();
        
        try {
            document.execCommand('copy');
            return true;
        } catch (err) {
            console.warn('传统复制失败', err);
            return false;
        } finally {
            document.body.removeChild(textarea);
        }
    };

    // 现代API检测
    const copyWithModernAPI = async () => {
        if (!navigator.clipboard) return false;
        
        try {
            await navigator.clipboard.writeText(commandText);
            return true;
        } catch (err) {
            console.warn('剪贴板API失败', err);
            return false;
        }
    };

    // 执行复制并处理结果
    const handleCopyResult = (success) => {
        const btn = document.querySelector('.copy-btn');
        if (!btn) return;
        
        const originalText = btn.innerHTML;
        btn.innerHTML = success 
            ? '<i class="fas fa-check"></i> 已复制'
            : '<i class="fas fa-exclamation-triangle"></i> 复制失败';
        
        setTimeout(() => {
            btn.innerHTML = originalText;
        }, 2000);
    };

    // 尝试现代API -> 回退传统方式
    (async () => {
        const success = await copyWithModernAPI() || copyFallback();
        handleCopyResult(success);
    })();
}

// 项目信息
const stats = {
    stars: 253,
    forks: 48,
    issues: 12
};

// 使用生命周期钩子替代DOMContentLoaded
onMounted(() => {
    console.log(`严小希项目统计: ${stats.stars} stars, ${stats.forks} forks`);
});
</script>


