}

/* 自适应视频容器 */
.video-container {
    position: relative;
    width: 100%;
    border-radius: 12px;
    overflow: hidden;
    box-shadow: var(--shadow);
    background: #000;
}

/* 核心: 使用padding技术保持16:9比例 */
.video-container::before {
    content: '';
    display: block;
    padding-top: 56.25%;
    /* 16:9 比例 (9/16 = 0.5625 * 100%) */
}

.video-container video {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
    outline: none;
}

/* 功能介绍板块 */
.video-info-box {
    background: linear-gradient(45deg, rgba(255, 179, 0, 0.1), rgba(255, 193, 7, 0.2));
    border-radius: 12px;
    padding: 25px;
    box-shadow: var(--shadow);
}

.video-controls {
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-wrap: wrap;
    gap: 15px;
    padding-bottom: 0px;
    margin-bottom: 0px;
    border-bottom: 1px solid var(--border);
}

.video-info {
    display: flex;
    align-items: center;
    gap: 15px;
    color: var(--gray);
    font-size: 15px;
}

.divider {
    height: 20px;
